'use client';
import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import { db } from '@/firebase/firebase';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { SearchParamsContext } from 'next/dist/shared/lib/hooks-client-context.shared-runtime';

export default function TimKiem() {
  const SearchParamsContext=useSearchParams();
  const  keyword  = SearchParamsContext.get('keyword');
  const [products, setProducts] = useState([]);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const snapshot = await getDocs(
          query(collection(db, 'products'), where('name', '>=', keyword), where('name', '<=', keyword + '\uf8ff'))
        );
        const data = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setProducts(data);
      } catch (error) {
        console.log('Lỗi khi lấy sản phẩm', error);
      }
    };
    fetchProducts();
  }, [keyword]);

  return (
    <div className="max-w-screen-xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-4">Tìm kiếm sản phẩm: {keyword}</h1>
      {products.length > 0 ? (
        <ul className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {products.map(product => (
            <li key={product.id} className="shadow-md rounded-lg p-4">
              <img src={product.imageUrl} alt={product.name} className="w-full h-64 object-cover" />
              <h2 className="text-xl font-bold">{product.name}</h2>
              <p className="text-gray-600">{product.description}</p>
              <p className="text-lg font-bold">{product.price.toLocaleString()} VNĐ</p>
            </li>
          ))}
        </ul>
      ) : (
        <p>Không có sản phẩm nào khớp với từ khóa "{keyword}"</p>
      )}
    </div>
  );
}
